import React from 'react';
import './App.css';

import {MuiThemeProvider} from '@material-ui/core/styles';
import theme from './theme.js';

import NavigationBar from './components/NavigationBar/NavigationBar.js';
import SwipeableViews from 'react-swipeable-views';
import Page from './components/Page.js';

import Home from './pages/Home.js';
import About from './pages/About.js';
import Projects from './pages/Projects/Page.js';


const pages = new Map([
  ['Projects', <Projects/>],
  ['About', <About/>]
]);

class App extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      tabIndex: 0
    }
  }

  handleTabChange(index) {
    this.setState({tabIndex: index});
  }

  renderNavigationBar() {
    return (
      <NavigationBar
	tabs={Array(...pages.keys())}
	tabIndex={this.state.tabIndex}
	onTabChange={index => this.handleTabChange(index)}
      />
    );
  }

  renderPages() {
    const pageComponents = [];
    for (const [name, page] of pages.entries()) {
      pageComponents.push(<Page key={name}>{page}</Page>);
    }
    return pageComponents;
  }

  render() {
    return (
      <MuiThemeProvider theme={theme}>
	<div>
	  {this.renderNavigationBar()}
	  <SwipeableViews
	    index={this.state.tabIndex}
	    onChangeIndex={index => this.handleTabChange(index)}
	  >
	    {this.renderPages()}
	  </SwipeableViews>
	</div>
      </MuiThemeProvider>
    );
  }
}

export default App;



